.background_image_container{
    background-color: blue;
    width: 900px;
    height: 300px;
    border-radius: 30px;
    margin: auto;
}
img{
    width: 100%;
    height: 100%;
    border-radius: 30px;
}
.change_pix{
    width: 90px;
    height: 50px;
    border-radius: 30px;
    background-color: lightpink;
    justify-content: center;
    align-items:center;
    display: flex;
    cursor: pointer;
}
.change_pix_container{
    margin-top: -50px;
    justify-content: flex-end;
    display: flex;
}
